.indicator {
    position: relative;
    width: 50px;
    height: 50px;
    background: #ccc;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.indicator span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: none;
    border-right: 3px solid #fff;
    border-bottom: 3px solid #fff;
}
